/*--------------------------------------------------------------
# Navigation
--------------------------------------------------------------*/
.main-navigation {
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: auto; }
  @media all and (min-width: 1000px) {
    .main-navigation {
      justify-content: flex-start;
      padding: 1rem; } }
  .main-navigation .xsszu-logo {
    height: auto;
    width: 50%;
    max-width: 250px;
    display: flex;
    order: 3;
    margin: auto; }
    @media all and (min-width: 1000px) {
      .main-navigation .xsszu-logo {
        width: 100%;
        margin: 2rem 0;
        order: 1; } }
  .main-navigation .menu-toggle {
    display: flex;
    border: none;
    width: 200px;
    margin: auto;
    justify-content: center;
    order: 1; }
    @media all and (min-width: 1000px) {
      .main-navigation .menu-toggle {
        display: none; } }
  .main-navigation .menu-main-menu-container {
    flex-direction: column;
    width: 100%;
    margin: auto;
    order: 2; }
    .main-navigation .menu-main-menu-container .primary-menu {
      display: flex;
      flex-direction: column;
      list-style: none;
      width: 50%;
      margin: 0 auto;
      padding: 0; }
      .main-navigation .menu-main-menu-container .primary-menu .menu-item {
        margin: 0 auto; }
      @media all and (min-width: 1000px) {
        .main-navigation .menu-main-menu-container .primary-menu {
          display: block;
          padding: 0;
          margin: 0;
          justify-content: left; } }

.menu-hide {
  display: none; }
  @media all and (min-width: 1000px) {
    .menu-hide {
      display: block; } }

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden; }
  .screen-reader-text:focus {
    border-radius: 3px;
    box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
    clip: auto !important;
    display: block;
    font-weight: bold;
    height: auto;
    left: 5px;
    line-height: normal;
    padding: 15px 23px 14px;
    text-decoration: none;
    top: 5px;
    width: auto;
    z-index: 100000;
    /* Above WP toolbar. */ }

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5rem; }

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5rem; }

.aligncenter {
  clear: both; }

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clearfix {
  clear: both;
  content: "";
  display: table; }

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
  margin: 0 0 1.5rem;
  /* Make sure select elements fit in widgets. */ }
  .widget select {
    max-width: 100%; }

/* Search widget. */
.widget_search .search-submit {
  display: none; }

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/* Posts and pages */
.sticky {
  display: block; }

.hentry {
  margin: 0 0 1.5rem; }

.updated:not(.published) {
  display: none; }

.single .byline,
.group-blog .byline {
  display: inline; }

.page-content,
.entry-content,
.entry-summary {
  margin: 1.5rem; }

.page-links {
  clear: both;
  margin: 0 0 1.5rem; }

/* Asides */
.blog .format-aside .entry-title,
.archive .format-aside .entry-title {
  display: none; }

/* Comments */
.comment-content a {
  word-wrap: break-word; }

.bypostauthor {
  display: block; }

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
  border: none;
  margin-bottom: 0;
  margin-top: 0;
  padding: 0; }

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
  max-width: 100%; }

/* Captions */
.wp-caption {
  margin-bottom: 1.5rem;
  max-width: 100%; }
  .wp-caption .wp-caption-text {
    margin: 0.8075rem 0; }

.wp-caption-text {
  text-align: center; }

/* Galleries*/
.gallery {
  margin-bottom: 1.5rem; }

.gallery-item {
  display: inline-block;
  text-align: center;
  vertical-align: top;
  width: 100%; }
  .gallery-columns-2 .gallery-item {
    max-width: 50%; }
  .gallery-columns-3 .gallery-item {
    max-width: 33.33%; }
  .gallery-columns-4 .gallery-item {
    max-width: 25%; }
  .gallery-columns-5 .gallery-item {
    max-width: 20%; }
  .gallery-columns-6 .gallery-item {
    max-width: 16.66%; }
  .gallery-columns-7 .gallery-item {
    max-width: 14.28%; }
  .gallery-columns-8 .gallery-item {
    max-width: 12.5%; }
  .gallery-columns-9 .gallery-item {
    max-width: 11.11%; }

.gallery-caption {
  display: block; }

/*--------------------------------------------------------------
# General Layout
--------------------------------------------------------------*/
@media all and (min-width: 1000px) {
  .site {
    display: flex;
    flex-direction: row; }
    .site .main-navigation {
      display: flex; } }
    @media all and (min-width: 1000px) and (min-width: 1000px) {
      .site .main-navigation {
        border-right: 2px solid grey;
        height: 100vh;
        width: 250px;
        position: fixed;
        flex-direction: column; } }

@media all and (min-width: 1000px) {
    .site .site-content {
      margin-left: 250px; } }

.site-footer {
  display: none; }
